import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import EleUI from '@/components/EleUI'
import EleUI_demo1 from '@/components/EleUI_demo1'
import AdminLogin from '@/components/AdminLogin'
import MainFrame from '@/components/MainFrame'
import GoodsMgr from '@/components/GoodsMgr'
import AdminHome from '@/components/AdminHome'
import AdminMenu from '@/components/AdminMenu'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:"/admin_login"
      //name: 'HelloWorld',
      //component: HelloWorld
    },{
      path:'/ele',
      name: 'EleUI',
      component: EleUI
    },{
      path:'/ele_1',
      name:"Ele_demo1",
      component: EleUI_demo1
    },{
      path:'/admin_login',
      name:"AdminLogin",
      component: AdminLogin
    },{
      path:'/main_frame',
      name:"MainFrame",
      component: MainFrame,
      children:[
        {path:"",name:"AdminHome",components:{default:AdminMenu,main_view:AdminHome}},
        {path:"goods_mgr",name:"GoodsMgr",components:{default:AdminMenu,main_view:GoodsMgr}}
      ]
    }
  ]
})



