<template>

  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>用户登录</span>
    </div>
    <div>
      <el-form ref="userFrm" :model="userFrm" status-icon :rules="rules" label-width="80px" class="demo-ruleForm">
        <el-form-item label="账号" prop="userName">
          <el-input v-model="userFrm.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="userFrm.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('userFrm')">登陆</el-button>
          <el-button @click="resetForm('userFrm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </el-card>
</template>

<script>
    import axios from 'axios'
    export default {
        data(){
          var checkUserName = (rule,value,callback)=>{
            if(!value){
              callback(new Error("用户名不能为空"));
            }else{
              callback();
            }
          };
          var checkPassword=(rule,value,callback)=>{
            if(!value){
              callback(new Error("请输入密码"));
            }else{
              callback();
            }
          };
          return{
            userFrm:{
              userName:"",
              password:""
            },
            rules:{
              userName:[{validator: checkUserName, trigger: 'blur'}],
              password:[{validator: checkPassword, trigger: 'blur'}]
            }
          }
        },
        methods:{
          submitForm(formName){
            this.$refs[formName].validate((valid)=>{
              if(valid){
                //发起请求
                var path = this.$Api.globalUrl+"/login";
                axios.get(path,{params:this.userFrm}).then((res)=>{
                  if(!res.data)
                  {
                    alert("用户名或密码不正确");
                    return;
                  }
                  //保存账号
                  this.$store.commit("setAdminUser",res.data);
                  //跳转页面
                  this.$router.push("/main_frame");
                },(res)=>{
                  alert("promise处理失败了:"+res);
                }).catch((msg)=>{
                  alert("promise异常:"+msg);
                })
              }else{
                alert("校验失败");
                return false;
              }
            })
          },
          resetForm(formName){
            this.$refs[formName].resetFields();
          }
        }
    }
</script>

<style scoped>
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 400px;
    margin:50px auto;
  }
</style>



