<template>
  <el-container style="height: 100%;">
    <el-header>
      <div class="logo">
        <i class="el-icon-setting"></i>MY系统-{{adminUser.userName}}
      </div>
      <div class="info">
        <el-dropdown>
          <span class="el-dropdown-link">
            <i class="el-icon-user-solid"></i>
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item><span @click="logout">注销</span></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown>
          <span class="el-dropdown-link">
            <i class="el-icon-s-tools"></i>
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>设置</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown>
          <span class="el-dropdown-link">
            <i class="el-icon-phone-outline"></i>
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>个人信息</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <router-view ></router-view>
      </el-aside>
      <el-main>
        <router-view name="main_view"></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "MainFrame",
        data(){
          return{
            adminUser:this.$store.getters.getAdminUser
          }
        },
        methods:{
          logout(){
            //清空用户对象
            this.$store.commit("setAdminUser",null);
            this.$router.push("/admin_login");
          }
        }
    }
</script>

<style scoped>
  .el-header{
    background-color: #4a4a4a;
    color: #333;
    line-height: 60px;
    position: relative;
  }
  .el-header .logo
  {
    position: absolute;
    left:0px;
    top:0px;
    width:190px;
    height:60px;
    color:white;
    padding-left: 10px;
    background-color: #24383a;
  }

  .el-header .info
  {
    position: absolute;
    top:0px;
    right:20px;
    width:200px;
    height:60px;
    text-align: left;
  }
  .el-header .info .el-dropdown{
    color: white;
    cursor: pointer;
    margin-right: 20px;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
  }
</style>



