<template>
  <el-menu
           :router="true"
           class="el-menu-vertical-demo"
           unique-opened
           text-color="#333333"
           active-text-color="orange"
  >
    <el-submenu v-for="m,mIndex in menu_modules" :key="mIndex" :index="''+mIndex">
      <template slot="title"><i :class="m.moduleIcon"></i>{{m.moduleText}}</template>
      <el-menu-item-group>
        <el-menu-item v-for="sm,smIndex in m.subModules" :index="''+mIndex+'-'+smIndex" @click="jump(mIndex,smIndex)" :key="smIndex">{{sm.moduleText}}</el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>

<script>
    import axios from 'axios'
    export default {
      name: "AdminMenu",
      data(){
        return{
          menu_modules:[]
        }
      },
      methods:{
        jump(mIndex,smIndex){
          var url = this.menu_modules[mIndex].subModules[smIndex].moduleUrl;
          console.log(url);
          this.$router.push({name:url});
        },
        loadModules(){
          //加载菜单模块
          var path = this.$Api.globalUrl+"/modules/list";
          //发起ajax请求
          axios.get(path).then((res)=>{
            this.menu_modules=res.data;
          })
        }
      },
      mounted:function () {
        this.loadModules();
      }
    }
</script>

<style scoped>

</style>



