<template>
  <div>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>商品管理</span>
      <el-button style="float: right; padding: 3px 3px" type="text" @click="handleAdd">商品添加</el-button>
    </div>
    <el-form :inline="true" :model="searchFrm" class="demo-form-inline">
      <el-form-item label="商品名称">
        <el-input v-model="searchFrm.goodsName" placeholder="商品名称"></el-input>
      </el-form-item>
      <el-form-item label="生产日期">
        <el-date-picker
          v-model="goodsDate"
          type="daterange"
          value-format="yyyy-MM-dd"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchGoods">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="pi.list"
      height="300"
      border
      style="width: 100%">
      <el-table-column
        prop="goodsId"
        label="编号"
        width="80">
      </el-table-column>
      <el-table-column
        prop="goodsName"
        label="名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="price"
        label="价格">
      </el-table-column>
      <el-table-column
        prop="address"
        label="产地">
      </el-table-column>
      <el-table-column
        prop="produceDate"
        label="生产日期">
      </el-table-column>
      <el-table-column
        prop="category.categoryName"
        label="分类">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="handleEdit(scope.$index,scope.row)">编辑</el-button>
          <el-popconfirm title="确定删除吗？" @onConfirm="handleDelete(scope.$index,scope.row)">
            <el-button slot="reference" size="mini" type="danger">删除</el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      style="margin-top:10px;"
      layout="sizes,prev, pager, next,jumper,->,total,slot"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="searchFrm.pageNum"
      :page-size="searchFrm.pageSize"
      :page-sizes="pageSizes"
      :total="pi.total"
      :hide-on-single-page="true"
      >
    </el-pagination>

  </el-card>
  <!--添加和修改窗口-->
  <el-dialog :title="goodsDialog.title"
              :visible.sync="goodsDialog.visible"
              :width="goodsDialog.width"
              @opened="handleDialogOpen" >
    <el-form :model="goodsDialog.goodsFrm" :rules="goodsDialog.rules" ref="goodsDialog.goodsFrm" :label-position="'left'">
      <el-form-item label="商品名称" :label-width="goodsDialog.lableWidth" prop="goodsName">
        <el-input v-model="goodsDialog.goodsFrm.goodsName" autocomplete="off" size="small"></el-input>
      </el-form-item>
      <el-form-item label="价格" :label-width="goodsDialog.lableWidth" prop="price">
        <el-input v-model="goodsDialog.goodsFrm.price" autocomplete="off"  size="small"></el-input>
      </el-form-item>
      <el-form-item label="产地" :label-width="goodsDialog.lableWidth" prop="address">
        <el-col :span="24">
          <el-select v-model="goodsDialog.goodsFrm.address"  size="small" style="width:100%">
            <el-option label="请选择" value=""></el-option>
            <el-option label="河南" value="河南"></el-option>
            <el-option label="北京" value="北京"></el-option>
            <el-option label="山东" value="山东"></el-option>
            <el-option label="上海" value="上海"></el-option>
          </el-select>
        </el-col>
      </el-form-item>
      <el-form-item label="生产日期" :label-width="goodsDialog.lableWidth" prop="produceDate">
        <el-col :span="24">
          <el-date-picker
            v-model="goodsDialog.goodsFrm.produceDate"
            type="date"
            value-format="yyyy-MM-dd"
            size="small"
            style="width:100%">
        </el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="分类" :label-width="goodsDialog.lableWidth" prop="category.categoryId">
        <el-col :span="24">
          <el-select v-model="goodsDialog.goodsFrm.category.categoryId"  size="small" style="width:100%">
            <el-option label="请选择" value=""></el-option>
            <el-option v-for="c,cIndex in categoryList" :key="cIndex" :label="c.categoryName" :value="c.categoryId"></el-option>
          </el-select>
        </el-col>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goodsDialog.visible = false">取 消</el-button>
      <el-button type="primary" @click="handleSubmit">确 定</el-button>
    </div>
  </el-dialog>
  </div>
</template>

<script>
    import axios from 'axios'
    export default {
      name: "GoodsMgr",
      data() {
        return {
          searchFrm:{
            goodsName:"",
            startDate:"",
            endDate:"",
            pageNum:1,
            pageSize:5
          },
          goodsDate:"", //查询日期区间
          pi: {}, //查询返回的分页数据
          pageSizes:[3,5,10], //每页记录数
          goodsDialog:{
            title:"商品添加",
            visible:false,
            lableWidth:"80px",
            width:"400px",
            goodsFrm:{
              goodsId:"",
              goodsName:"",
              price:"",
              address:"",
              produceDate:"",
              category:{
                categoryId:"",
                categoryName:""
              }
            },
            rules:{
              goodsName:[
                {required:true,message:'请输入商品名称',trigger:'blur'},
                {min:2,max:30,message:'长度在2到30个字符',trigger:'blur'}
              ],
              price:[
                {required:true,message:'请输入价格',trigger:'blur'}
              ],
              address:[
                {required:true,message:'请选择产地',trigger:'change'},
                {min:2,max:10,message:'长度在2到10个字符',trigger:'change'}
              ],
              produceDate:[
                {type:'string',required:true,message:'请选择日期',trigger:'blur'}
              ],
              'category.categoryId':[
                {required:true,message:'请选择分类',trigger:'change'}
              ]
            }
          },
          categoryList:[] //分类列表
        }
      },
      mounted:function () {
       this.searchGoods();
      },
      methods:{
        searchGoods(){
          if(this.goodsDate){
            this.searchFrm.startDate=this.goodsDate[0];
            this.searchFrm.endDate=this.goodsDate[1];
          }
          var goods_path = this.$Api.globalUrl+"/goods/list";
          axios.get(goods_path,{params:this.searchFrm}).then((res)=>{
            this.pi=res.data;
          });
          var category_path = this.$Api.globalUrl+"/category/list";
          axios.get(category_path).then((res)=>{
            this.categoryList=res.data;
          })
        },
        handleSizeChange(val){
          //切换每页大小
          this.searchFrm.pageSize=val;
          this.searchGoods();
        },
        handleCurrentChange(val){
          //切换当前页
          this.searchFrm.pageNum=val;
          this.searchGoods();
        },
        handleDialogOpen(){
          console.log("---打开对话框---");
          //清除表单的校验
          this.$refs["goodsDialog.goodsFrm"].clearValidate();
        },
        handleAdd(){
          console.log("---通过添加按钮打开页面---");
          this.goodsDialog.title="商品添加";
          this.goodsDialog.visible=true;
          this.goodsDialog.goodsFrm.goodsId="";
          this.goodsDialog.goodsFrm.goodsName="";
          this.goodsDialog.goodsFrm.price="";
          this.goodsDialog.goodsFrm.address="";
          this.goodsDialog.goodsFrm.produceDate="";
          this.goodsDialog.goodsFrm.category.categoryId="";
        },
        handleEdit(index,row){
          console.log("---通过修改按钮打开页面---");
          this.goodsDialog.title="商品修改";
          this.goodsDialog.visible=true;
          this.goodsDialog.goodsFrm.goodsId=row.goodsId;
          this.goodsDialog.goodsFrm.goodsName=row.goodsName;
          this.goodsDialog.goodsFrm.price=row.price;
          this.goodsDialog.goodsFrm.address=row.address;
          this.goodsDialog.goodsFrm.produceDate=row.produceDate;
          this.goodsDialog.goodsFrm.category.categoryId=row.category.categoryId;

          console.log("--------edit--------");
          console.log(this.$refs['goodsDialog.goodsFrm']);
          console.log(index);
          console.log(row);
        },
        handleDelete(index,row){ //行号和数据
          let path = this.$Api.globalUrl+"/goods/delete/"+row.goodsId;
          axios.delete(path).then((res)=>{
            this.searchGoods();
          })
        },
        handleSubmit(){
          //console.log(this.$refs);
          this.$refs["goodsDialog.goodsFrm"].validate((valid)=>{
            if(valid){
              let path =this.$Api.globalUrl+"/goods/";
              if(this.goodsDialog.goodsFrm.goodsId){
                path+="update"
              }else{
                path+="add";
              }
              alert(path);
              //提交表单
              axios.post(path,this.goodsDialog.goodsFrm).then((res)=>{
                if(res.data.code==0){
                  alert("提交成功");
                  this.goodsDialog.visible=false;
                  //刷新显示列表
                  this.searchGoods();
                }
              })
            }else{
              alert("校验失败");
              return false;
            }
          })
        }
      }
    }
</script>

<style scoped>

</style>



